.Example {
    display: grid;
    margin-bottom: 4rem;
}

.Example .Title {
    padding: var(--padding);
    margin: 0;
    
}

.Example img{
    width: 100%;
}

.Example .About {
    padding: var(--padding);
    margin: 0;
}

.Spec table {
    width: 100%;
}

.Spec td {
    margin: 0;
    padding: 1rem 0;
    /* box-shadow: rgba(136, 136, 136, 0.377) 0px 2px 5px; */
    border-bottom: 1px solid rgba(128, 128, 128, 0.473);
}

.Spec td:first-child {
    /* background-color: red; */
    padding-right: 2rem;
    /* padding: 0rem 2rem; */
    
}

.Spec tr td:first-child {
    /* background-color: red; */
    font-weight: 600;
}

@media(min-width: 1000px) {
    .Example {
        grid-template-columns: 30rem 1fr;
        /* grid-template-rows: auto 1fr; */
        /* background-color: red; */
    }
    .Example .Title {
        grid-column: 2 / 3;
        padding: 0 calc(var(--padding) * 1.5);
    }
    .Example img {
        grid-column: 1 / 2;
    }
    .Example .About {
        padding: 0 calc(var(--padding) * 1.5);
        margin: 0;
    }
    .Example .Spec {
        /* grid-column: 2 / 3; */
    }
}