.App {
  position: relative;
  /* font-family: 'Fira Code', sans-serif; */
  /* text-align: center; */
}

.Cover {
  color: white;
  text-align: center;
  background-image: url(./imgs/laptop46454.jpg);
  background-position: center;
  background-color: rgba(0, 0, 0, 0.4);
  background-blend-mode: multiply;
  background-repeat: no-repeat;
  /* background: rgba(0, 0, 0, 0.233); */
  /* backdrop-filter: rgba(0,0,0,0); */
  background-size: cover;
  width: 100vw;
  height: 100vh;
  display: grid;
  align-content: center;
}

.Cover h1 {
  padding: 0;
  margin: 0;
}

.Prolog {
  padding: 2rem;
}

.BackButton {
  --color: rgb(115, 241, 231);
  position: fixed;
  /* display: block; */
  font-family: 'Fira Code';
  color: var(--color);
  font-size: 3rem;
  /* padding: 10px; */

  width: 5rem;
  height: 5rem;

  /* margin: 1rem; */
  bottom: 1rem;
  right: 1rem;
  border-radius: 100%;
  border: 1px solid var(--color);
  outline: none;

  background-color: rgba(255, 255, 255, 0.8);
}








